import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import {FormsModule} from '@angular/forms';
import { StormpathModule, StormpathConfiguration } from 'angular-stormpath';
import { AppComponent } from './app.component';
import { PeopleComponent } from './people/people.component';
import {Routes,RouterModule} from '@angular/router';
import { HomeComponent } from './home/home.component';
import { WorkComponent } from './work/work.component';
import { ContactComponent } from './contact/contact.component';
import { StudentsComponent } from './students/students.component';
import { FacultyComponent } from './faculty/faculty.component';
import { ResearchComponent } from './research/research.component';

import { PostsComponent } from './home/posts/posts.component';
import { PostComponent } from './home/posts/post/post.component';
import { BackDirective } from './back.directive';

const appRoutes: Routes=[{path:'', component:HomeComponent },{path:'research', component:ResearchComponent },{path:'home', component:HomeComponent },{path:'faculty', component:FacultyComponent },{path:'students', component:StudentsComponent },{path:'people', component:PeopleComponent },{path:'work', component:WorkComponent },{path:'contact', component:ContactComponent }]

export function stormpathConfig(): StormpathConfiguration {
  let spConfig: StormpathConfiguration = new StormpathConfiguration();
  spConfig.endpointPrefix = 'https://{DNS-LABEL}.apps.stormpath.io';
  return spConfig;
 }

@NgModule({
  declarations: [
    AppComponent,
    PeopleComponent,HomeComponent, WorkComponent, ContactComponent, StudentsComponent, FacultyComponent, ResearchComponent,  PostsComponent, PostComponent, BackDirective
    
  ],
  imports: [RouterModule.forRoot(appRoutes),
    BrowserModule,
    HttpClientModule,FormsModule
    
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



