import { Component, OnInit, ViewChild } from '@angular/core';
import { Post } from './post.model';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})
export class PostsComponent implements OnInit {

@ViewChild('f') PostForm:NgForm;
post:Post;



  onCreate:boolean=false;

  posts:Post[]=[ new Post( '../../../assets/images/iit-brand.png' , 'first post','here is the first news ','posted on 4/2/2018 by ' ),new Post( '../../../assets/images/iit-brand.png' , 'second post','here is the second news ','posted on 4/2/2018 by ' ) ]

  constructor() { }

  ngOnInit() {
  }
onCreatePost()
{
  this.onCreate=true;
  
}
onSubmit(){
  
  console.log("form has been submitted");
  console.log(this.PostForm);
this.post=new Post(this.PostForm.value.imageUrl,this.PostForm.value.title,this.PostForm.value.text,this.PostForm.value.footerText);


this.posts.push(this.post);


console.log("the data entered in the newly created post by you is as follows ");
console.log(this.post);
console.log('post has been added succcessfully');
this.PostForm.reset();

}

}



